import React from 'react';
import AnchorLink from 'react-anchor-link-smooth-scroll';
import {Translate, withLocalize} from "react-localize-redux";
import translation from "../translations/translate";

import AnimalIcon from '../images/icons/animal-fat.svg'
import FishIcon from '../images/icons/fish-fat.svg'
import BioIcon from '../images/icons/biodiesel.svg'
import AcidIcon from '../images/icons/acids.svg'
import VegetableIcon from '../images/icons/vegetable.svg'
import ByprodIcon from '../images/icons/by-prod.svg'
import ReactSVG from "react-svg";

const prods = [
    {to: '#animal', name: 'animal', img: AnimalIcon},
    {to: '#acids', name: 'acids', img: AcidIcon},
    {to: '#biodiesel', name: 'biodiesel', img: BioIcon},
    {to: '#byproducts', name: 'byproducts', img: ByprodIcon},
    {to: '#fish', name: 'fish', img: FishIcon},
    {to: '#vegetable', name: 'vegetable', img: VegetableIcon},
];

function Products(props) {
    props.addTranslation(translation);
    return (
        <div className='products'>
            <div className="row container mx-auto text-center">
                    {prods.map(({to, img, name}, id) => (
                        <div key={id} className='col-6 col-md-4 col-lg-2'>
                            <AnchorLink href={to}><ReactSVG className='products-icon' src={img} descr='icon'/></AnchorLink>
                            <h4 className='products-text'>
                                <Translate id={"cards." + name + ".title"}/>
                            </h4>
                        </div>
                    ))}
            </div>
        </div>
    );
}

export default withLocalize(Products);
